/* ServicesBanner.css */
.sb_services-banner-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: #333333;
    color: white;
    padding: 5% 3%;
    font-family: 'Roboto', sans-serif;
}

.sb_services-content-wrapper {
    flex: 1;
    position: relative;
    left: 0;
    max-width: 50%;
}

.sb_services-title {
    font-size: clamp(24px, 4vw, 36px);
    margin-bottom: 3%;
    line-height: 1.2;
}

.sb_services-description {
    font-size: clamp(14px, 2vw, 16px);
    margin-bottom: 5%;
    line-height: 1.5;
}

.sb_button-group-container {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
}

.sb_btn {
    padding: 10px 20px;
    border: none;
    border-radius: 40px;
    cursor: pointer;
    font-size: clamp(14px, 1.5vw, 15px);
    transition: background-color 0.3s, transform 0.2s;
    height: clamp(50px, 8vw, 65px);
    width: clamp(150px, 20vw, 200px);
    font-family: 'Roboto', sans-serif;
}

.sb_btn-quote, .sb_btn-trial {
    background-color: #cccccc;
    color: #000000;
    font-weight: 700;
}

.sb_btn:hover {
    background-color: #bbbbbb;
    transform: scale(1.05);
}

.sb_services-image-wrapper {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    max-width: 50%;
}

.sb_services-banner-image {
    max-width: 100%;
    height: auto;
    object-fit: cover;
}

@media (max-width: 1024px) {
    .sb_services-content-wrapper {
        left: 0;
    }
}

@media (max-width: 768px) {
    .sb_services-banner-container {
        flex-direction: column;
        text-align: center;
        padding: 8% 5%;
    }

    .sb_services-content-wrapper,
    .sb_services-image-wrapper {
        max-width: 100%;
    }

    .sb_button-group-container {
        justify-content: center;
    }

    .sb_services-image-wrapper {
        margin-top: 5%;
    }
}

@media (max-width: 480px) {
    .sb_services-banner-container {
        padding: 10% 5%;
    }

    .sb_button-group-container {
        flex-direction: column;
        align-items: center;
    }

    .sb_btn {
        width: 80%;
    }
}